import React from 'react'
import { StaticQuery, graphql } from 'gatsby';

 class Modal extends React.Component{
    
     constructor(props){
         super(props);
         this.state = {isModalVisible: true};
     }
     render(){
         if (this.state.isModalVisible === false){
             return null;
         }
         else return(
             
            <div className="modal">
                <StaticQuery query={graphql`
                    query{
                        site {
                            siteMetadata {
                                modal_content
                            }
                        }
                    }
                `} render={data => (
                    this.showModal(data.site.siteMetadata.modal_content)
                )}/>   
            </div>
         )
     }
     componentDidMount(){
         if (localStorage.getItem("isModalShowed") === "true"){
            this.setState({isModalVisible:false})
         }
         else{
            window.setTimeout(()=>{
                this.setState({isModalVisible: false})
             },5000)
             localStorage.setItem("isModalShowed",true)
         } 
     }
     showModal(content){
         if(content!==''){
             return <div className='modal__inside_box modal__inside_box--slided'>{content}</div>;
         }
     }

 }



export default Modal;