import React from 'react'
import './LayoutStyle.css'
import Header from './Header'
import { StaticQuery } from 'gatsby'
import { Helmet } from "react-helmet"
import Footer from './Footer'
import favicon from "../images/favicon-96x96.png"

class Layout extends React.Component{
    render(){
        return(
            
            <div className="layout__container">

                <StaticQuery query={graphql`
                query {
                    site {
                        siteMetadata {
                            title
                        }
                    }
              }`
            } render={data=>(
                
                    <Header logo={data.site.siteMetadata.title}/>
                   
                )}/>

            {this.props.children}
            <Helmet>
            <meta charSet="utf-8" />
            <title>Kamil Michna</title>
            <link rel="shortcut icon" type="image/png" href={favicon}/>
            </Helmet>   
            <Footer content="Ta strona została stworzona w Gatsby.js i jest ciągle w fazie rozwoju. Jeżeli masz dla mnie propozycje współpracy, lub chcesz się skontaktować oto mój email: kamil.michna28@gmail.com"/> 
            </div>
        
        )
    }
} 

export default Layout
